.web-page.contact-form{
    background: #AA06010D;
    padding-bottom: 92px;
    @include phone(){
        padding-bottom: 48px;
    }
    .container{
        max-width: 800px;
        margin: 0 auto;
        @include pad(){
            max-width: 64.6%;
        }
        @include phone(){
            max-width: 87.2%;
        }

    }
    .title{
        color:$primary;
        padding-bottom: 24px;
        &+p{
            font-size: 20px;
            margin-bottom: 20px;
        }
    }
    .form{
        margin-bottom: 48px;
    }

    .form-group{
        margin-bottom: 16px;
        label{
            display: block;//
            margin-bottom: 5px;
        }
        input.form-control,textarea{
            display: block;//block
            width: 100%;//rows cols=
            background: transparent;
            border:1.3px solid gray;
            outline: 0;//
            padding: 14px 16px;
        }
        &.check{//input lable
            // input[type="checkbox"]{
            //     display: block;
            // }
            // align-items: center;//input
            align-items: baseline;//style
            label{
                margin-bottom:0;
            }
            //input[type="checkbox]
        }
    }
    
    .send{
        text-align: center;
        .btn{
            display: inline-block;//ablock
            width: 180px;
            // height: 54px;//
            padding: 16px 0;
            font-size: 16px;
            color: #fff;
            background-color: $primary;
            @include md(){
                width: 136px;
                padding: 7px 0;
            }
        }
    }
    
}